import { OnInit, Component } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';

import itemList from '../../assets/json/item.json';
import dropList from '../../assets/json/drop.json';
import monsterList from '../../assets/json/monster.json';
import questList from '../../assets/json/quest.json';
import questSteplistList from '../../assets/json/quest_steplist.json';
import questStepinfoList from '../../assets/json/quest_stepinfo.json';


export interface ItemData {
  id: number;
  name: string;
  type: string
  description: string;
}

/**
 * @title Data table with sorting, pagination, and filtering.
 */
@Component({
  selector: 'item',
  styleUrls: ['item.component.css'],
  templateUrl: 'item.component.html',
})
export class ItemComponent implements OnInit {

  id: any;
  item: any;
  drops: any;
  infos: any;

  constructor(
    private route: ActivatedRoute,
    private router: Router,
  ) {
    document.getElementsByTagName('mat-sidenav-content')[0].scrollTo(0, 0);
  }

  ngOnInit () {
    this.id = this.route.snapshot.paramMap.get('id');
    this.item = itemList.root[''].find((item: any) => { return item['_'] === this.id; });
    this.drops = dropList.root.drop.filter((drop: any) => {
      for (let i = 1; i <= 40; i++) {
        const d = drop['_item'+i];
        if (d && d === this.id) return true;
      }
      return false;
    }).map((drop: any) => {
      const monster = monsterList.root.npc.find((monster: any) => { return monster['_'] === drop['_']});
      if (!monster || +drop['_'] > 4213 ) return { type: 'drop', data: drop }
      return { type: 'monster', data: monster };
    });

    console.log('itemname', this.item['_']);

    this.infos = questStepinfoList.root.info.filter((info: any) => {
      return info['_'] === '' && info['_'] === this.item['_'];
    }).map((info: any) => {
      return {
        steps: questSteplistList.root[''].filter((steplist: any) => {
          if (steplist['_item1'] && steplist['_item1'] === info['_']) return true;
          if (steplist['_item2'] && steplist['_item2'] === info['_']) return true;
          if (steplist['_item3'] && steplist['_item3'] === info['_']) return true;
          if (steplist['_item4'] && steplist['_item4'] === info['_']) return true;
          if (steplist['_item5'] && steplist['_item5'] === info['_']) return true;
          return false;
        }).map((steplist: any) => {
          return questList.root[''].filter((quest) => {
            if (quest['_00'] && quest['_00'] === steplist['_']) return true;
            if (quest['_01'] && quest['_01'] === steplist['_']) return true;
            if (quest['_02'] && quest['_02'] === steplist['_']) return true;
            if (quest['_03'] && quest['_03'] === steplist['_']) return true;
            if (quest['_04'] && quest['_04'] === steplist['_']) return true;
            if (quest['_05'] && quest['_05'] === steplist['_']) return true;
            if (quest['_06'] && quest['_06'] === steplist['_']) return true;
            if (quest['_07'] && quest['_07'] === steplist['_']) return true;
            if (quest['_08'] && quest['_08'] === steplist['_']) return true;
            return false;
          });
        }),
        count: info['_']
      };
    });

    console.log('infos', this.infos);

    console.log('drops', this.drops);
  }

}
